.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  fill: currentColor;
  position: relative;
}

[data-stroke="true"] {
  stroke: currentColor;
}
[data-fill="true"] {
  fill: currentColor;
}

.icon-arrow-left {
  width:(14/14)*1em;
}
.icon-basket {
  width:(13/16)*1em;
}
.icon-close {
  width:(10/9)*1em;
}
.icon-instagram {
  width:(14/14)*1em;
}
.icon-search {
  width:(16/16)*1em;
}
.icon-whatsapp {
  width:(14/14)*1em;
}
