.carousel {
  margin-left: -$grid-gutter-width/4;
  margin-right: -$grid-gutter-width/2;
  margin-bottom: 1.875rem;

  @include media-breakpoint-up(md) {
    margin-left: -$grid-gutter-width/2;
  }

  &__item {
    padding-left: 8px;
    padding-right: 8px;

    @include media-breakpoint-up(md) {
      padding-left: 16px;
      padding-right: 16px;
    }
  }


  &__image {
    display: block;
    margin-bottom: 8px;
  }

  &__title {
    display: inline-block;
  }

  &__footer {
    text-align: center;
  }


  .slick-slide {
    width: calc((100vw - #{$grid-gutter-width}) / 2.2 );

    @include media-breakpoint-up(sm) {
      width: 240px;
    }

    @include media-breakpoint-up(md) {
      width: 330px;
    }

    @include media-breakpoint-up(lg) {
      width: 228px;
    }
  }
}
