//
.nav-trigger {
  &__btn {

    @include media-breakpoint-down(md) {
      margin-left: -10px;
    }
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 46px;
    overflow: hidden;
    border: 1px solid transparent;
    padding: 10px 10px;
    font-weight: 400;
    color: $dark;
    cursor: pointer;
    user-select: none;
    background-color: transparent;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 0;
    text-indent: 100%;
    white-space: nowrap;

    .nav-trigger__icon {

      @include media-breakpoint-up(lg) {
        width: 24px;
      }
      display: block;
      position: relative;
      z-index: 10;
      width: 16px;
      height: 1px;
      background-color: currentColor;

      &::before, &:after {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 1px;
        transform: translateZ(0);
        backface-visibility: hidden;
        transition: transform .3s;
        background-color: currentColor;
      }

      &::before {
        @include media-breakpoint-up(lg) {
          transform: translateY(-8px) rotate(0deg);
        }
        transform: translateY(-6px) rotate(0deg);
      }

      &::after {
        @include media-breakpoint-up(lg) {
          transform: translateY(8px) rotate(0deg);
        }
        transform: translateY(6px) rotate(0deg);
      }
    }


    &.nav-trigger_close .nav-trigger__icon, &.nav-trigger_body .nav-trigger__icon {
      background-color: rgba($dark, 0);

      &::before {
        transform: translateY(0) rotate(45deg);
      }

      &::after {
        transform: translateY(0) rotate(-45deg);
      }
    }
  }

  &__body {
    display: flex;
    flex-direction: column;
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    width: 330px;
    height: 100vh;
    overflow: hidden;
    transform: translateX(-100%);
    transition: visibility 0s, opacity .3s, transform .3s;
    padding-bottom: 30px;
    background-color: transparentize($white, .05);
    -webkit-overflow-scrolling: touch;
    opacity: 0;

    &.active {
      transform: translateX(0%);
      opacity: 1;
    }
  }

  &__header {
    @include media-breakpoint-up(lg) {
      padding: 18px 36px 30px 36px;
    }
    padding: 0 20px 20px 46px;
  }

  &__scroll {
    height: 100%;
    overflow: auto;
    padding: 0 32px 30px;
  }
}

.navbar {

  .nav-trigger_close:after {
    content: "";
    position: fixed;
    z-index: 2;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: transparent;
  }
  &-dark {
    .nav-trigger__btn {
      color: $white;
    }
  }
}
