.footer {

  @include media-breakpoint-up(lg) {
    font-size: 12px;
  }
  flex: 0 0 auto;
  margin-top: auto;
  border-top: 1px solid transparentize($dark, 0.9);
  padding-top: 26px;
  padding-bottom: 50px;
  overflow-x: hidden;

  &__title {
    font-weight: 500;
    margin-bottom: 6px;

    &_level {
      letter-spacing: 0.31em;
    }
  }

  &__link {

    @include hover {
      color: #585856;
    }
    display: inline-block;
    padding: 6px 0;
    line-height: (20px / 12px);
    transition: color .2s ease-in-out;
  }

  &__divider {
    @include media-breakpoint-down(md) {
      border-top: 1px solid transparentize($dark, 0.9);
      padding-top: 30px;
      margin-top: 30px;

      &:after {
        content: "";
        border-top: 1px solid transparentize($dark, 0.9);
        position: absolute;
        top: -1px;
        right: 100%;
        width: 15vw;
      }

      &:last-of-type {
        &:after {
          right: auto;
          left: 100%;
        }
      }
    }
  }
}
