.catalog {
  &__container {
    padding-top: 10px;
    padding-bottom: 3.875rem;
  }

  &__nav {
    margin-bottom: 24px;



    .flickity-viewport {
      margin-right: -15px;
      margin-left: -15px;
      padding-left: 15px;
      @include media-breakpoint-up(lg) {
        margin-left: -15px;
      }
      .flickity-slider {
        margin-left: 15px;
      }

      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: -10px;
        bottom: 0;
        width: 34px;
        pointer-events: none;
        background: linear-gradient(270deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%);
        z-index: 1;
      }

      &:after {
        content: "";
        position: absolute;
        top: 0;
        right: -10px;
        bottom: 0;
        width: 64px;
        pointer-events: none;
        background: linear-gradient(270deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
      }
    }



    &-item {
      padding-left: .5rem;
      padding-right: .5rem;

      &:first-child {
        padding-left: 0;
      }
      &:last-child {
        padding-right: 0;
      }
    }

    &-link {

      @include hover-focus() {
        color: $dark;
      }
      display: block;
      position: relative;
      padding: $nav-link-padding-y 0;
      white-space: nowrap;

      &:not(.active) {

        @include hover-focus {
          &:after {
            background-color: transparentize($dark, 0);
          }
        }
        &:after {
          content: "";
          position: absolute;
          right: 0;
          bottom: 6px;
          left: 0;
          height: 1px;
          transition: background-color .2s ease-in-out;
          background-color: transparentize($dark, .8);
        }
      }

      &.active {
        font-weight: 600;
      }
    }
  }

  &__items {
    @include media-breakpoint-down(xs) {
      padding-left: 0;
      padding-right: 0;
      .col-6 {
        padding-left: 0;
        padding-right: 0;
      }
    }
  }

  &__item {
    margin-bottom: 1.875rem;

    &-image {
      display: block;
      margin-bottom: 8px;
    }

    &-title {
      display: inline-block;
    }

    &-footer {
      text-align: center;
    }
  }

  &__show-more {
    display: flex;
    justify-content: center;
    padding-top: 1rem;
  }
}
