.product {
  &__container {
    padding-bottom: 20px;

    @include media-breakpoint-up(lg) {
      padding-top: 26px;
    }
  }
  &__body {
    padding-bottom: 48px;
  }

  &__header {

    @include media-breakpoint-up(lg) {
      text-align: left;
    }
    margin-bottom: 26px;
    text-align: center;
  }

  &__title {

    @include media-breakpoint-up(lg) {
      font-size: 14px;
    }
    font-size: 16px;
    margin-bottom: 0;
  }

  &__basket-icon {
    margin-right: 5px;
  }

  &__question {
    &-btn {

      .icon {
        margin-right: 5px;
      }
      &:not(:last-child) {
        margin-right: 12px;
      }
    }
  }
}

.dimension {

  @include media-breakpoint-up(lg) {
    display: block;
    margin-bottom: 26px;
  }
  margin-bottom: 32px;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__group {
    display: flex;
    margin-left: -8px;
    margin-right: -8px;
    margin-bottom: 6px;
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 52px;
    height: 52px;
    border: 1px solid transparentize($dark, .8);
    margin-left: 8px;
    margin-right: 8px;
    text-align: center;

    @include media-breakpoint-up(lg) {
      width: 35px;
      height: 32px;
    }

    &:hover, &.active {
      border-color: transparentize($dark, 0);
    }
  }

  &__table-btn {

    @include hover-focus {
      &:after {
        border-bottom-color: transparentize($dark, 0);
      }
    }
    display: inline-block;
    position: relative;
    padding: 0;
    border-width: 0;

    &:after {
      content: "";
      position: absolute;
      right: 0;
      bottom: -2px;
      left: 0;
      transition: border-bottom-color .2s ease-in-out;
      border-bottom: 1px solid transparentize($dark, .8);
    }
  }
}

.color {

  @include media-breakpoint-up(lg) {
    display: block;
  }
  margin-bottom: 28px;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__title {
    margin-bottom: 8px;
  }

  &__group {
    display: flex;
    margin-bottom: 8px;
    @include media-breakpoint-up(lg) {
      margin-left: -5px;
      margin-right: -5px;
    }
  }

  &__btn {
    margin-left: 5px;
    margin-right: 5px;
    padding: 2px;

    @include media-breakpoint-up(lg) {
      margin-left: 3px;
      margin-right: 3px;
    }

    &_white {
      &:hover, &.active, .color__value {
        border-color: transparentize($dark, .8) !important;
      }
    }

    &:hover, &.active {
      border-color: currentColor;
    }
  }

  &__value {
    display: block;
    width: 52px;
    height: 24px;
    border: 1px solid currentColor;
    background-color: currentColor;

    @include media-breakpoint-up(lg) {
      width: 35px;
      height: 16px;
    }
  }
}

.gallery {
  display: flex;
  padding-left: 0;
  padding-right: 0;

  @include media-breakpoint-up(lg) {
    padding-left: $grid-gutter-width/2;
    padding-right: $grid-gutter-width/2;
  }

  &__thumb {
    width: 48px;
    margin-right: 32px;
    //min-height: 300px;
    position: absolute;
    z-index: -10;
    pointer-events: none;
    visibility: hidden;

    @include media-breakpoint-up(lg) {
      position: relative;
      visibility: visible;
      pointer-events: auto;
      z-index: 1;
    }

    &-item {
      padding-bottom: 10px;
    }

    .slick-slide {
      cursor: pointer;
      transform: translateY(-20px);
    }
  }

  &__main {
    flex: 1;
    overflow: hidden;
  }

  &__picture {
    width: 100%;

    .slick-slide {
      img {
        margin-left: auto;
        margin-right: auto;
      }
    }

    &.slick-dotted {
      margin-bottom: 20px;
    }
  }
}
