.header {
  @include media-breakpoint-up(lg) {
    height: 80px;
    border-bottom-color: transparent;
  }
  display: flex;
  align-items: center;
  border-bottom: 1px solid transparentize($dark, .9);
  min-height: 50px;

  &__basket {
    &-link {
      @include hover-focus {
        .header__basket-text:after {
          content: "";
          position: absolute;
          right: 0;
          bottom: -2px;
          left: 0;
          border-bottom: 1px solid $dark;
        }
      }

      position: absolute;
      transform: translateY(-50%);
      .icon {
        top: 0;
        color: #d3d3d3;
        font-size: 15px;
      }
    }

    &-text {
      position: relative;
    }
  }

  &_divider {
    border-bottom: 1px solid #e9e9e9;
  }

  &__search {
    position: absolute;
    width: 300px;
    right: 0;
  }
}

.menu {
  padding-right: 4px;
  align-items: center;
  &__link {
    position: relative;
    padding: 8px 8px;
    font-size: 16px;
    display: flex;
  }

  &__icon {

    color: inherit;
    @include media-breakpoint-up(lg) {
      top: -2px;
    }
  }

  &__basket-count {
    position: absolute;
    top: 2px;
    right: 2px;
    width: 12px;
    height: 12px;
    background-color: $dark;
    color: $white;
    font-size: 10px;
    border-radius: 50%;
    text-align: center;
    line-height: 0.75rem;
  }
}

.m-search {
  position: relative;
  z-index: 10;

  &:hover,
  &.active {
    .m-search__btn {
      padding-left: 1.125rem;
      padding-right: 1.125rem;
      border-color: rgba(33, 33, 33, 0.1);
      background-color: $white;

      &:hover {
        border-color: $dark;
      }
    }

    .m-search__control {
      width: 260px;
      padding-left: $input-padding-x-lg;
      padding-right: $input-padding-x-lg;
      opacity: 1;
    }
  }

  &__btn {
    font-size: 16px;
    padding: 0.375rem .5rem;
    border-color: transparent;
    transition: padding .2s;
    background-clip: padding-box;

    &:focus {
      box-shadow: none !important;
    }
  }

  &__control {
    flex: 0 0 auto;
    width: 0;
    padding: $input-padding-y-lg 0;
    position: relative;
    opacity: 0;
    transition:all .4s;

    @include media-breakpoint-down(xs) {
      position: absolute !important;
      right: 100%;
      max-width: calc(100vw - 115px);
    }

    @include media-breakpoint-down(md) {
      height: calc(1.5em + 1rem - 1px);
    }
  }
}

.navbar-dark {
  .menu {
    &__link {
      color: $white;
    }
  }
  .m-search__btn {
    color: $white;
  }
  .m-search {
    &:hover,
    &.active {
      .m-search__btn {
        color: $dark;
      }
    }
  }
}
